.el-time-spinner {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.el-badge, .el-badge__content, .el-spinner {
  display: inline-block;
}

.el-spinner {
  vertical-align: middle;
}

.el-spinner-inner {
  animation: rotate 2s linear infinite;
  width: 50px;
  height: 50px;
}

.el-spinner-inner .path {
  stroke: #ececec;
  stroke-linecap: round;
  animation: dash 1.5s ease-in-out infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1,150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90,150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90,150;
    stroke-dashoffset: -124;
  }
}

.el-badge {
  position: relative;
  vertical-align: middle;
}

.el-badge__content {
  background-color: #ff4949;
  border-radius: 10px;
  color: #fff;
  font-size: 12px;
  height: 18px;
  line-height: 18px;
  padding: 0 6px;
  text-align: center;
  white-space: nowrap;
  border: 1px solid #fff;
}

.el-badge__content.is-dot {
  width: 8px;
  height: 8px;
  padding: 0;
  right: 0;
  border-radius: 50%;
}

.el-badge__content.is-fixed {
  top: 0;
  right: 10px;
  position: absolute;
  -ms-transform: translateY(-50%) translateX(100%);
  transform: translateY(-50%) translateX(100%);
}

.el-rate__icon, .el-rate__item {
  position: relative;
  display: inline-block;
}

.el-badge__content.is-fixed.is-dot {
  right: 5px;
}

.el-card {
  border: 1px solid #D3DCE6;
  border-radius: 4px;
  background-color: #fff;
  overflow: hidden;
}

.el-card__header {
  padding: 18px 20px;
  border-bottom: 1px solid #D3DCE6;
  box-sizing: border-box;
}

.el-card__body {
  padding: 20px;
}

.el-rate {
  height: 20px;
  line-height: 1;
}

.el-rate__item {
  font-size: 0;
  vertical-align: middle;
}

.el-rate__icon {
  font-size: 18px;
  margin-right: 6px;
  color: #C6D1DE;
  transition: .3s;
}

.el-rate__decimal, .el-rate__icon .path2 {
  position: absolute;
  top: 0;
  left: 0;
}

.el-rate__icon.hover {
  -ms-transform: scale(1.15);
  transform: scale(1.15);
}

.el-rate__decimal {
  display: inline-block;
  overflow: hidden;
}

.el-rate__text {
  font-size: 14px;
  vertical-align: middle;
}

.el-steps {
  font-size: 0;
}

.el-steps > :last-child .el-step__line {
  display: none;
}

.el-step.is-horizontal, .el-step.is-vertical .el-step__head, .el-step.is-vertical .el-step__main, .el-step__line {
  display: inline-block;
}

.el-steps.is-horizontal {
  white-space: nowrap;
}

.el-step {
  position: relative;
  vertical-align: top;
}

.el-step.is-vertical .el-step__main {
  padding-left: 10px;
}

.el-step__line {
  position: absolute;
  border-color: inherit;
  background-color: #c0ccda;
}

.el-step__line.is-vertical {
  width: 2px;
  box-sizing: border-box;
  top: 30px;
  bottom: 0;
}

.el-step__line.is-horizontal {
  top: 15px;
  height: 2px;
  left: 30px;
  right: 0;
}

.el-step__line.is-icon.is-horizontal {
  right: 4px;
}

.el-step__line-inner {
  display: inherit;
  border-width: 1px;
  border-style: solid;
  border-color: inherit;
  transition: all 150ms;
  width: 0;
  height: 0;
}

.el-step__icon {
  display: block;
  line-height: 28px;
}

.el-step__icon > * {
  line-height: inherit;
  vertical-align: middle;
}

.el-step__head {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: transparent;
  text-align: center;
  line-height: 28px;
  font-size: 28px;
  vertical-align: top;
  transition: all 150ms;
}

.el-step__head.is-finish {
  color: #20a0ff;
  border-color: #20a0ff;
}

.el-step__head.is-error {
  color: #ff4949;
  border-color: #ff4949;
}

.el-step__head.is-success {
  color: #13ce66;
  border-color: #13ce66;
}

.el-step__head.is-process, .el-step__head.is-wait {
  color: #c0ccda;
  border-color: #c0ccda;
}

.el-step__head.is-text {
  font-size: 14px;
  border-width: 2px;
  border-style: solid;
}

.el-step__head.is-text.is-finish {
  color: #fff;
  background-color: #20a0ff;
  border-color: #20a0ff;
}

.el-step__head.is-text.is-error {
  color: #fff;
  background-color: #ff4949;
  border-color: #ff4949;
}

.el-step__head.is-text.is-success {
  color: #fff;
  background-color: #13ce66;
  border-color: #13ce66;
}

.el-step__head.is-text.is-wait {
  color: #c0ccda;
  background-color: #fff;
  border-color: #c0ccda;
}

.el-step__head.is-text.is-process {
  color: #fff;
  background-color: #c0ccda;
  border-color: #c0ccda;
}

.el-step__main {
  white-space: normal;
  padding-right: 10px;
  text-align: left;
}

.el-step__title {
  font-size: 14px;
  line-height: 32px;
  display: inline-block;
}

.el-step__title.is-finish {
  font-weight: 700;
  color: #20a0ff;
}

.el-step__title.is-error {
  font-weight: 700;
  color: #ff4949;
}

.el-step__title.is-success {
  font-weight: 700;
  color: #13ce66;
}

.el-step__title.is-wait {
  font-weight: 400;
  color: #99a9bf;
}

.el-step__title.is-process {
  font-weight: 700;
  color: #475669;
}

.el-step__description {
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.el-step__description.is-finish {
  color: #20a0ff;
}

.el-step__description.is-error {
  color: #ff4949;
}

.el-step__description.is-success {
